
.SideBar
{
    width:25%;
    background-color:rgb(83, 0, 46);
    min-height: 100vh;
    color:white;
    box-sizing: border-box;
}
@media (max-width:739px)
{
    .SideBar{
        width: 100%;
        min-height: unset;
        .roomList{
            transition: height 4s linear;
        }
    }
}